<template>
    <v-form id="post-add" @submit.prevent>
        <v-container>
            <v-layout column>
                <v-flex>
                    <v-text-field
                        v-model="title"
                        label= "Tytuł posta"
                        required>
                    </v-text-field>
                </v-flex>
                <v-flex>
                    <v-text-field
                        v-model="img"
                        label= "Url obrazka"
                        required>
                    </v-text-field>
                </v-flex>
                <v-flex>
                    <v-select
                        v-model="category"
                        :items="categories"
                        label= "Kategoria"
                        required>
                    </v-select>
                </v-flex>
                <v-flex>
                    <vue-editor v-model="content"></vue-editor>
                </v-flex>
                <v-btn @click="npost" dark color="orange" 
                    depressed :disabled="!formIsValid" type.submit> Zapisz </v-btn> 
            </v-layout>
        </v-container>
    </v-form>
</template>
<script>
    import { VueEditor } from 'vue2-editor'
    import { db } from '../main'

    export default {
    components: {
        VueEditor
    },
    data() {
        return {
            content: '',
            title: '',
            img: '',
            category: '',
            short: '',
            categories: [
                'Polska',
                'Europa',
                'Świat',
                'Poradnik'
            ],
            button: true
        }
    },
    computed: {
        formIsValid(){
            return this.title !== "" &&
            this.img !== "" &&
            this.content !== "" &&
            this.category !== ""
        }
    },
    methods: {
        post () {
            fetch('http://localhost:3000/api/posts',{
                method: 'POST',
                body: JSON.stringify({           
                    title: this.title,
                    img: this.img,
                    date: new Date(),
                    text: this.content,
                    category: this.category,
                }),
                headers:{
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            })
            .then(res => res.json())
            .then(res => console.log(res))
            .catch(err => console.log(err))
        },
        npost() {
            db.collection('posts').add({
                    title: this.title,
                    img: this.img,
                    date: "2018-01-01",
                    text: this.content,
                    category: this.category
            })
            .then(function(docRef) {
                console.log("Document written with ID: ", docRef.id)
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });
        }
    }
   }
</script>

<style lang="scss" scoped>
#editor-container {
    height: 375px;
  }

#post-add{
    input{
        padding: 0.6rem;
        box-sizing: border-box;
        width: 95%;
        margin-bottom: 1rem; 
    }
 
}
</style>
