import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import PostAdd from './views/Post-add.vue'
import PostView from './views/Post-view.vue'
import Polska from './views/Polska.vue'
import Poradnik from './views/Poradnik.vue'
import Europa from './views/Europa.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/admin',
      name: 'admin',
      component: PostAdd
    },
    {
      path: '/post/:id',
      name: 'post',
      component: PostView,
      props: true
    },
    {
      path: '/Polska',
      name: 'Polska',
      component: Polska
    },
    {
      path: '/Poradnik',
      name: 'Poradnik',
      component: Poradnik
    },
    {
      path: '/Europa',
      name: 'Europa',
      component: Europa
    }
  ]
})