<template>
<div>
     <v-layout id='posts-list' class="pa-1" 
        v-for="post in fposts" :key="post.id" row wrap>
        <v-flex xs12>           
            <v-card router :to="{path: `/post/${post.id}`}" flat >      
                <v-layout row wrap>
                    <v-flex xs12 md6>
                        <v-img
                            :src="post.img"
                            aspect-ratio = "2"
                        ></v-img>
                    </v-flex>                           
                    <v-flex xs12 md6 class="">
                        <v-card-title class="headline pb-1">
                            <div class="text-truncate">{{ post.title }}</div>
                        </v-card-title>
                        <v-card-title class="">
                            <truncate clamp="Czytaj dalej" :length="120" less="Show Less" type="html" :text="post.text"></truncate>
                        </v-card-title>
                        <v-card-text>{{ post.category }} | {{post.date}}</v-card-text>
                    </v-flex>                   
                </v-layout>           
            </v-card>
        </v-flex>   
    </v-layout> 
</div> 
</template>

<script>
import truncate from 'vue-truncate-collapsed';
import { db } from '../main'
import firebase from 'firebase/app'

export default {
    
    data () {
        return{
            posts: [],
            fposts: []
        }
        
    },
    props: [
        'category'
    ],
    components: {
        truncate
    },
    methods: {
        fetchPosts () {
            if (this.category === undefined) {
            db.collection("posts").get()
            .then(querySnapshot => querySnapshot.forEach(doc => {
                let id = doc.id
                let data= doc.data()
                this.fposts.push({id, ...data})
                })
            )
            }else {
            db.collection("posts").where("category","==",this.category).get()
            .then(querySnapshot => querySnapshot.forEach(doc => {
                let id = doc.id
                let data= doc.data()
                this.fposts.push({id, ...data})
                })
            )
            }

        }
    },
    created(){
        this.fetchPosts()


    },
    
}
</script>

