<template>
<v-container>
    <v-layout>
        <v-flex xs>
            <v-card id="post-view" >
            <v-img id="post-img" :src="post.img" aspect-ratio = "2">
                <v-layout align-end fill-height pa-3 white--text >
                    <v-flex>
                        <h1>{{post.title}}</h1>
                        <h5>{{post.category}} | {{post.date}}</h5>
                    </v-flex>
                </v-layout> 
            </v-img>
            <v-card-title class="font-weight-black subheading" id="post-short">{{post.short}}</v-card-title>       
            <v-card-text id="post-text" v-html="post.text"></v-card-text>
            </v-card>
        </v-flex>
    </v-layout>

</v-container>
</template>

<script>
import { db } from '../main'


export default {
    name: "PostView",
    props: ['id'],
    data () {
        return {
            post: {}
        }
    },
    methods: {
        // loadPost(){
        //     fetch(`http://localhost:3000/api/posts/${this.id}`)
        //         .then (res => res.json())
        //         .then (data => this.post = data)
        //         .catch (err => console.log(err))
        // },
        loadPost(){
            db.collection("posts").doc(this.id).get()
            .then(doc => this.post = doc.data())
        }
},
    created(){
        this.loadPost()
    }
}
</script>

